import React from 'react';
import './ProjectsComponent.css';
import ProjectsView from '../ProjectsView/ProjectsView';

class ProjectsComponent extends React.Component{
    constructor(){
        super();
        this.state = {
            projectsView: false
        }
        this.handleProjectsView = this.handleProjectsView.bind(this)
    }
    handleProjectsView(){
        this.setState({
            projectsView: !this.state.projectsView
        })
    }
        render(){
            const { projectsView } = this.state;
            return(
                <div className='ProjectsComponent'>
                    <h1>{this.props.projectName}</h1>
                    <img alt='project' src={this.props.projectImg}></img>
                    <div onClick={this.handleProjectsView} className='View-btn'><a>View</a></div>
                    {projectsView && <ProjectsView skillUseArray={['React','HTML','CSS']} closeView={this.handleProjectsView}/>}
                </div>
            )
        }
    }

export default ProjectsComponent;