import React, { Component } from 'react';
import './ProjectsView.css';
import SkillUse from './skillUse/SkillUse';

export default class ProjectsView extends Component {
  render(props) {
    return (
      <div className='ProjectsView'>
        <div className='ProjectImage'> {/* Acá hay que poner un carrusel de imagenes */}
           <div id='left'><span>←</span></div>
           <div id='right'><span>→</span></div>
           <div onClick={this.props.closeView} id='close-view'><span>X</span></div>
        </div>
        <div className='ProjectDesc'>
            <h1>Lorem Ipsum</h1>
            <p>Aliquip excepteur amet fugiat qui sint aliquip cupidatat duis quis ipsum ex cupidatat. Reprehenderit nisi mollit tempor deserunt aute dolore ex. Excepteur non et labore proident nostrud eu quis. Consequat tempor ipsum fugiat commodo sit. Sint culpa ea excepteur exercitation non est. Adipisicing sunt fugiat Lorem deserunt deserunt aute. Quis ipsum aliquip consectetur officia.</p>
            <div className='skillsUseProject'>
              <ul>{this.props.skillUseArray.map(
                skill =>{
                    return(
                     <li><SkillUse skillUse={skill} key={skill}/></li> 
                    )
                }
              )}
              </ul>
            </div>
        </div>
      </div>
    )
  }
}
