import React from 'react';
import './Rank.css';
import SkillComponent from './SkillComponent';


const Rank = ()=>{
    
    return(
        <div className='Rank'>
            <SkillComponent skill='JS' percentageSkill={10}/>
            <SkillComponent skill='REACT' percentageSkill={5}/>
            <SkillComponent skill='UX' percentageSkill={1}/>
            <SkillComponent skill='PSD' percentageSkill={2}/>
            <SkillComponent skill='NODEJS'/>
            <SkillComponent skill='EXPRESS'/>
            <SkillComponent skill='MKTDG'/>
        </div>
    )
}

export default Rank;